import ErrorMessage from "../errorMessage/ErrorMessage"
import { Link } from "react-router-dom";


const Page404 = () => {
  return (
    <div>
      <ErrorMessage/>
      <p style={{'textAlign': 'center', 'display': 'block', 'fontWeight': 'bold', 'fontSize': '24px', 'margin-bottom': '20px'}}>Page doesn't exist</p>
      <Link to='/' style={{'textAlign': 'center', 'display': 'block', 'fontWeight': 'bold', 'fontSize': '30px'}}>Back to the main page</Link>
    </div>
  )
}

export default Page404;